<template>
  <van-tabs type="card">
    <van-tab title="喜欢">
      <scroll-view class="scroll-view">
        <div class="list">
          <div class="list-item" v-for="(e, i) in collection" :key="i">
            <p @click="playCollection(i)">{{e.song}}<span>{{e.singer}}</span></p>
            <van-icon name="cross" @click="removeCollection(i)"/>
          </div>
        </div>
        <div v-if="!collection.length" class="none">空空如也~</div>
      </scroll-view>
    </van-tab>
    <van-tab title="历史">
      <scroll-view class="scroll-view">
        <div class="list">
          <div class="list-item" v-for="(e, i) in historySongs" :key="i">
            <p @click="playHistory(i)">{{e.song}}<span>{{e.singer}}</span></p>
            <van-icon name="cross" @click="removeHistorySongs(i)"/>
          </div>
        </div>
        <div v-if="!historySongs.length" class="none">空空如也~</div>
      </scroll-view>
    </van-tab>
  </van-tabs>
</template>
<script>
import { mapState, mapMutations, mapActions } from 'vuex'
import ScrollView from '#/ScrollView'

export default {
  name: 'Collection',
  components: {
    ScrollView,
  },
  computed: {
    ...mapState('music', ['collection']),
    ...mapState('player', ['historySongs']),
  },
  methods: {
    ...mapMutations('music', ['removeCollection']),
    ...mapMutations('player', ['playerList', 'removeHistorySongs']),
    ...mapActions('player', ['play']),
    playCollection(i) {
      this.playerList(this.collection)
      this.play(i)
    },
    playHistory(i) {
      this.playerList(this.historySongs)
      this.play(i)
    },
  },
}
</script>
<style lang="scss" scoped>
/deep/ .van-icon:before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }
/deep/ .van-icon {
  flex: 0 0 .6rem;
  font-size: 14px; }
.van-tabs--card {
  padding-top: 10px; }
.scroll-view {
  padding-top: 10px;
  height: calc(100vh - 92px);
}
.none {
  font-size: .3rem;
  text-align: center;
  padding: .2rem; }
.list {
  background-color: #fff;
  &-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #eee;
    padding: 0 .2rem;
    p {
      flex: auto;
      font-size: 14px;
      padding: 10px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      span {
        color: #aaa;
        margin-left: .2rem;
      }
    }
  }
}
</style>


